* {
  box-sizing: border-box;
  max-width: 100%;
}

.revisions {
  position: fixed;
  top: 0;
  left: 0;
  display: grid;
  grid-template-rows: 70px auto 76px 1fr;
  grid-template-columns: 1fr 280px;
  width: 100%;
  height: 100%;
  grid-template-areas: "header header"
    "notice notice"
    "editor spacer"
    "editor sidebar";
  z-index: 99999999;
  background-color: $background-color;
}

.revisions-header {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-row: header;
  grid-column: header;
  padding: 20px 40px;
}

.revisions-header .revision-header--right-group {
  display: grid;
  justify-content: right;
}

.revisions-header h1 {
  font-size: 15px;
  margin: 0;
}

.revision-view--button-group {
  border: 1px solid #7b8083;
}

.revision-view--button-group button {
  display: inline !important;
  width: 150px;
  text-align: center;
  background-color: #fff;
  border: 1px solid #fff;
  box-shadow: none !important;
}

.revisions-editor {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-areas: "editor-left editor-right";
  grid-column-gap: 18px;
  grid-row: editor;
  grid-column: editor;
  overflow-y: auto;
  padding: 0 0 30px 40px;
  align-content: start;
}

.is-single-view .revisions-editor {
  grid-template-columns: none;
  grid-template-areas: none;
}

.revisions-message {
  grid-row: notice;
  grid-column: notice;
  padding: 0 40px;
}

.revisions-message-toast {
  grid-row: notice;
  grid-column: notice;
  padding: 0 20px;
  background-color: #fff;
  margin: 0 40px;
  border: 1px solid #d0d3d5;
  border-left: 5px solid #dba369;
  display: flex;
  justify-content: space-between;
  align-items: center;

  button {
    color: #7b8083;
  }

}

.revisions-message:first-child:last-child {
  grid-row-start: header;
  grid-row-end: editor;
  grid-column: header;
  padding-right: 40px;
}

.revisions-message p,
.is-root-container.block-editor-block-list__layout {
  padding: 23px 30px;
  background-color: #fff;
}

.revisions-editor-left {
  grid-row: editor-left;
  grid-column: editor-left;
}

.revisions-editor-right {
  grid-row: editor-right;
  grid-column: editor-right;
}

.revisions-editor-center {
  grid-row: editor-center;
  width: 100%;
  max-width: 1000px;
  margin: 0 auto;
}

.revisions-editor-header {
  position: sticky;
  top: 0;
  z-index: 99999999999;
  background-color: $background-color;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 0 30px;
}

.revisions-editor-headerRight {
  display: flex;
}

.revisions-editor-headerTitle {
  margin-right: 15px;
}

.revisions-editor-headerTitle h1 {
  font-size: 15px;
  line-height: 21px;
  margin: 0;
}

.revisions-editor-header button {
  margin-left: auto;
}

.revisions-editor-headerMeta {
  margin: 0;
  font-size: 12px;
}

.revision-restore-button {
  margin-left: 15px;
}

.revisions-sidebar {
  grid-row: sidebar;
  grid-column: sidebar;
  overflow-y: auto;
  padding: 0 20px;
}

.editor-styles-wrapper::after {
  content: "";
  display: table;
  clear: both;
}

.dropdown {
  cursor: pointer;
  display: flex;
  align-items: center;
  background-color: #fff;
  border: 1px solid $wp-grey-dark-200;
  border-radius: 5px;
  padding: 10px;
  width: 100%;

  li {
    margin-bottom: 0;
  }
}

.dropdown-date {
  font-weight: 300;
}

.dropdownPopover .components-popover__content {
  min-width: 350px;
}

.dropdownPopover .dropdownOptions {
  margin: 0;
}

.dropdownPopover .dropdownOptions .dropdownOption {
  cursor: pointer;
  margin: 0;
}

.dropdown-option {
  cursor: pointer;
  margin-bottom: 0;
}

.dropdown-button {
  width: 100%;
  height: 100%;
  font-size: 14px;
  font-weight: normal;
  padding: 20px;
}

button.dropdown-button:not(.is-selected) {
  background-color: #fff !important;
}

button.dropdown-button.is-selected {
  background-color: $wp-grey-light-400;
}
