.revisionsTimeline-listItem {
  display: flex;
  flex-wrap: wrap;
  font-family: -apple-system, BlinkMacSystemFont, sans-serif;
  font-weight: 500;
}

.revisionsTimeline-listItem.isSelected {
  & :hover {
    cursor: default;
  }
  & button:focus {
    border: none;
    box-shadow: none;
  }
}

.revisionsTimeline-listItem .listItem-title {
  display: flex;
  align-items: center;
  margin-bottom: 0px;
  font-weight: 500;
  text-align: left;
  font-size: 13px;
}

.revisionsTimeline-listItem .listItem-nameDate {
  font-size: 12px;
  text-align: left;
  color: $wp-grey-dark-200;
}

.revisionsTimeline-listItem .listItem-title .dashicon,
.revisionsTimeline-listItem .listItem-title svg {
  margin-left: 5px;
  color: $wp-blue;

  &:hover {
    color: $wp-blue-tint-40;
  }

  &.revision--publish-updated {
    color: $wp-grey-dark-500;

    &:hover {
      color: $wp-grey-dark-200;
    }
  }
}

.revisionsTimeline-listItem.isSelected .listItem-title .dashicon {
  color: $wp-blue;
}

.revisionsTimeline-listItem.isSelected .listItem-title,
.revisionsTimeline-listItem.isSelected .listItem-nameDate {
  color: $wp-blue;
}

.revisionsTimeline-list {
  &::before {
    border-left: none !important;
  }
  .revisionsTimeline-listItem {
    align-items: center;
    justify-content: flex-start;
    flex-wrap: wrap;
    padding-left: 20px;
    border-left: 1px solid #b5bcc2;
    margin-top: -10px;
    padding-top: 10px;

    &:last-child {
      border-left: none;
    }
  }
}

.revisionsTimeline-listItem .components-button {
  flex-wrap: wrap;
  justify-content: center;
  height: auto;
  border: none;
  flex-direction: column;
  align-items: flex-start;
  padding: 0;
}

.revisionsTimeline-listButton {
  text-align: center;

  button {
    margin-bottom: 2em;
  }
}

.revisionsTimeline-list {
  position: relative;
  padding-left: 20px;
  margin-top: 0;

  &::before {
    border-left: 1px solid $wp-grey-light-800;
    content: "";
    position: absolute;
    top: 7px;
    bottom: 40px;
  }
}

.revisionsTimeline-listItem {
  position: relative;
  padding-left: 20px;
  margin-bottom: 0px;
  padding-bottom: 15px;
}

.revisionsTimeline-listItem:last-child {
  border-left: none;
}

.revisionsTimeline-listItem .listItem-innerWrapper .listItem-title::before {
  content: "";
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background: #fff;
  border: 1px solid $wp-grey-light-800;
  position: absolute;
  left: -6px;
  top: 10px;
}

.revisionsTimeline-listItem.isSelected .listItem-innerWrapper .listItem-title::before {
  background: $wp-blue-tint-40; /* Blue Tint 40% */
  border: 2px solid $wp-blue-medium; /* Medium Blue */
  left: -7px;
}

.revisionsTimeline-listItem .listItem-innerWrapper {
  display: flex;
  flex-direction: column;
  background: none !important;
  box-shadow: none !important;
  outline: none !important;
  outline-offset: initial !important;
  line-height: 1.5;
}

.revisionsTimeline-paginate {
  margin: 10px 0;
  text-align: center;
}

.revision-filter {
  margin-bottom: 30px;
}
