// ins/del block
.wp-block-newspress-revision-ins .wp-block .editor-block-list__block-edit,
.wp-block-newspress-revision-del .wp-block .editor-block-list__block-edit {
  padding: 15px;
}

*[data-type="newspress/revision-column"] {
  padding-left: 15px !important;
}

*[data-type="newspress/revision-variable"] [data-changes="true"].wp-block,
*[data-type="newspress/revision-del"].wp-block,
*[data-type="newspress/revision-ins"].wp-block {
  padding-left: 5px !important;
  padding-right: 5px !important;
}

*[data-type="newspress/revision-variable"] [data-changes="true"],
ins,
*[data-type="newspress/revision-ins"],
ins[data-insert-type="block"],
del,
*[data-type="newspress/revision-del"],
del[data-insert-type="block"] {
  text-decoration: none;
}

*[data-type="newspress/revision-variable"] [data-changes="true"],
*[data-type="newspress/revision-ins"] {
  background-color: $ins-background;
}

*[data-type="newspress/revision-variable"] [data-changes="true"][data-change-type="ins"] .wp-block,
*[data-type="newspress/revision-ins"] .wp-block {
  padding: 9px !important;
  background-color: $ins-background;

  /* core block tweaks */
  ul[data-type="core/list"],
  ol[data-type="core/list"] {
    margin-left: 15px !important;
  }
}

ins {
  background-color: $ins-tag;
}

*[data-type="newspress/revision-variable"] [data-changes="true"][data-change-type="del"],
*[data-type="newspress/revision-del"] {
  background-color: $del-background;
}

*[data-type="newspress/revision-variable"] [data-changes="true"][data-change-type="del"] .wp-block,
*[data-type="newspress/revision-del"] .wp-block {
  padding: 9px !important;
  background-color: $del-background;
}

del {
  background-color: $del-tag;
}

*[data-type="newspress/revision-updated"] {
  background-color: $updated-background;
}

*[data-type="newspress/revision-updated"] .wp-block {
  padding: 9px !important;
  background-color: $updated-background;
}

[data-type="newspress/revision-title"] h2 {
  margin-top: 0;
}
